@import 'var';

*,
*::before,
*::after{
  box-sizing: border-box;
}

body {
  background-color:  #f8f8ff;
  font-family: 'Cairo', Arial, sans-serif;
  color: black;
  font-size: 100%;
}
@import 'typo';
/*########################################## --- HEADER & NAV
############################################ 
############################################ */

header{
  line-height: 1;
  min-height: 50vh;
  background-color: #e6e1dc;
}




/*########################################## --- Section
############################################ 
############################################ */

main{
  max-width: 1600px;
  margin: 0 auto;
  min-height: 100vh;
}

section{
  margin: 0 auto;
  padding: 5vmax 0 3vmax 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-bottom: 3px solid var(--c-accent);
}

.reverse{
  flex-direction: column-reverse;
}
.flexBox{
  display: flex;
  gap: .6em;
  flex-direction: row;
  flex-wrap: wrap;
}


.leftContent{
  flex: 2;
  min-width: 400px;
}

.sectionHead{
  margin-bottom: 1em;
}
.sectionHead h1{
  font-size: clamp(1.6em, 4vw, 6em);
  line-height: 1;
  margin-bottom: .2em;
  text-transform: uppercase;
  letter-spacing: .06em;
}
.sectionHead h6{
  font-size: 21px;
  line-height: 1.4;
  margin-bottom: .8em;

}

article{
  /* min-width: 400px; */
  margin: 0 0 0  auto;
  padding: 0 .6em;
}

aside{
  flex: 1;
  min-width: 200px;
  padding: 0 .6em;
}

/*########################################## --- Bilder
############################################ 
############################################ */

img{
  max-width: 100%;
  height: auto;
}
figcaption{
  font-size: 16px;
}

/*########################################## --- Footer
############################################ 
############################################ */


footer{
  background-color: $col-accent;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}
footer a{
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  color: whitesmoke;
}
.wrapper{
  width: 96%;
  padding: 3vw .5em; 
  margin: 0 auto;
  text-align: right;
}
